:root {
    --color-txt-nav: #5b00fb
}

body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Arial, Helvetica, sans-serif;
}

header {
    width: 100%;
    height: 60px;
    display: flex;
}

/* containerBrand */
header .containerBrand {
    width: 20%;
    min-width: 200px;
    height: 100%;
    display: flex;
    justify-content: center;
}

header .containerBrand img {
    transform: scale(0.55);
}

/* containerList */
header .containerItemList {
    width: 70%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

header .containerItemList ul {
list-style: none;
display: flex;
margin: 0;
justify-content: flex-end;
}

header .containerItemList ul li {
    margin: 10px;
    color: var(--color-txt-nav);
    cursor: pointer;
}

/* containerCart */
header .containerCart {
    width: 10%;
    min-width: 72px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

header .containerCart .containerLength {
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    cursor: pointer;
}

header .containerCart .containerLength .cantCart {
    position: absolute;
    width: 20px;
    height: 20px;
    background-color: var(--color-txt-nav);
    color: white;
    text-align: center;
    top: 0;
    left: 80%;
    border-radius: 50%;
}